<template>
  <div class="guestbook">
    <div class="message">
      <p class="p1">欢迎灌水交流，别看我长时间不发博，博主可一直都在线~</p>
      <p class="p2">
        友链只交换技术类个人博客或相关行业。博客长期稳定在线，所以也需要你的站尽量稳定，三天两头打不开的我会直接删除友链不再做通知。
      </p>
      <p class="p3">博主Q联系:MzcxNjA0Nzg1</p>
    </div>
    <MessageWrapp :name="'留言'" :num="comLen" :datas="comment" />
    <FandByPage
      :page="page"
      :total="total"
      :limit="limit"
      :pNumber="pNumber"
      @click="change"
    />
    <Inputs :isShow="$store.state.isShow" @click="isShowClick" />
  </div>
</template>

<script>
import Inputs from "../components/compins/Inputs";
import FandByPage from "../components/compins/FandByPage";
import MessageWrapp from "../components/compins/MessageWrapp";
import { setComment } from "../utils/index";

export default {
  data() {
    return {
      page: 1,
      total: 100,
      limit: 10,
      pNumber: 5,
      comment: [],
      comLen: null,
    };
  },
  components: {
    Inputs,
    FandByPage,
    MessageWrapp,
  },
  mounted() {
    setComment(this, -2,this.findByPageHandle);
  },
  methods: {
    change(i) {
      this.page = i;
      this.findByPageHandle();
    },
    findByPageHandle() {
      let start = (this.page - 1) * this.limit;
      let stop = start + this.limit;
      let item = this.comment.slice(start, stop);
      this.item = item;
    },
    isShowClick(result) {
      this.$store.commit("getIsShow", result);
    },
  },
};
</script>

<style lang="less">
.guestbook {
  .message {
    width: 100%;
    font-size: 14px;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
    border-radius: 4px;
    padding: 20px;
    box-sizing: border-box;
    margin-bottom: 20px;
    color: #333;
    .p1 {
      margin: 10px 0 25px;
    }
    .p2,
    .p3 {
      color: red;
      margin: 10px 0;
    }
  }
}
</style>
